import { createSlice } from "@reduxjs/toolkit"
import { v4 } from "uuid"


const tasksSlice = createSlice({
  name: 'tasks',

  initialState: [
    {
      id: v4(),
      tabId: 'abc',
      body: 'Clean room',
      completed: true
    },
    {
      id: v4(),
      tabId: 'abc',
      body: 'Learn js',
      completed: false
    },
    {
      id: v4(),
      tabId: 'abc',
      body: 'Wash dishes',
      completed: true
    },
  ],

  reducers: {

    addTask: (state, action) => {
      const { tabId, body } = action.payload

      state.push({
        id: v4(),
        tabId,
        body,
        completed: false
      })
    },

    deleteTask: (state, action) => {
      const { id } = action.payload
      const taskToBeDeleted = state.find(task => task.id === id)
      state.splice(state.indexOf(taskToBeDeleted), 1)
    },

    toggleCompleted: (state, action) => {
      const { id } = action.payload
      const task = state.find(t => t.id === id)
      task.completed = !task.completed
    },

    editTask: (state, action) => {
      const { id, newValue } = action.payload
      state.find(task => task.id === id).body = newValue
    }

  }
})

export const {

  addTask,
  deleteTask,
  toggleCompleted,
  editTask

} = tasksSlice.actions

export default tasksSlice.reducer
