import { Box, Button, Typography, withStyles } from "@material-ui/core"
import { connect } from "react-redux"
import { toggleAddTabDialogVisibility } from "store/dialogsSlice"
import { deleteTab } from "store/tabsSlice"

const Header = (props) => {
  const openAddTabDialog = () => props.toggleAddTabDialogVisibility()
  const handleDeleteTab = () => props.deleteTab()

  return (
    <Box
      display={'flex'}
      alignItems={'center'}
      justifyContent={'space-between'}
    >
      <Typography variant={'h4'}>
        TodoList
      </Typography>
      <Box
        display={'flex'}
        gridColumnGap={5}
      >
        <StyledButton
          onClick={handleDeleteTab}
          variant={'contained'}
          color={'default'}
        >
          delete tab
        </StyledButton>
        <StyledButton
          onClick={openAddTabDialog}
          variant={'contained'}
          color={'primary'}
        >
          new tab
        </StyledButton>
      </Box>
    </Box>
  )
}

const StyledButton = withStyles({
  root: {
    borderRadius: '0',
    boxShadow: 'none'
  }
})(Button)

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps =  {
  toggleAddTabDialogVisibility,
  deleteTab
}

export default connect(mapStateToProps, mapDispatchToProps)(Header)
