import { createSlice } from "@reduxjs/toolkit"


const dialogsSlice = createSlice({
  name: 'dialogs',

  initialState: {
    editDialogVisibility: false,
    currentTaskEditingId: null,

    addTabDialogVisibility: false
  },

  reducers: {

    toggleEditDialogVisibility: (state, action) => {
      state.editDialogVisibility = !state.editDialogVisibility
    },

    setCurrentTaskEditingId: (state, action) => {
      const { id } = action.payload
      state.currentTaskEditingId = id
    },

    toggleAddTabDialogVisibility: (state, action) => {
      state.addTabDialogVisibility = !state.addTabDialogVisibility
    }

  }
})

export const {
  toggleEditDialogVisibility,
  setCurrentTaskEditingId,
  toggleAddTabDialogVisibility
} = dialogsSlice.actions

export default dialogsSlice.reducer
