import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from "@material-ui/core"
import { useState } from "react"
import { connect } from "react-redux"
import { toggleAddTabDialogVisibility } from "store/dialogsSlice"
import { addTab } from "store/tabsSlice"

const AddTab = (props) => {
  const [inputValue, setInputValue] = useState('')

  const handleClose = () => props.close()
  const handleSubmit = () => {
    props.add({ name: inputValue })
    props.close()
  }

  return (
    <Dialog open={props.dialogVisibility} onClose={handleClose}>
      <DialogTitle>Add Tab</DialogTitle>
      <DialogContent>
        <TextField
          autoFocus
          id="name"
          label={'Tab Name'}
          fullWidth
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleSubmit} color="primary">
          Create
        </Button>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  )
}

const mapStateToProps = (state) => ({
  dialogVisibility: state.dialogs.addTabDialogVisibility
})

const mapDispatchToProps = (dispatch) => {
  return {
    close: (payload) => dispatch(toggleAddTabDialogVisibility(payload)),
    add: (payload) => dispatch(addTab(payload))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(AddTab)
