import { Button, Dialog, DialogActions, DialogContent, DialogTitle, TextField } from "@material-ui/core"
import { useState } from "react"
import { connect } from 'react-redux'
import { toggleEditDialogVisibility } from "store/dialogsSlice"
import { editTask } from "store/tasksSlice"


const EditTask = (props) => {
  const [inputValue, setInputValue] = useState('')

  const handleInputValueChange = (e) => setInputValue(e.target.value)
  const handleClose = () => props.toggleVisibility()

  const handleSubmit = () => {
    if (!inputValue) return
    props.editTask({ id: props.editingTask, newValue: inputValue })
    setInputValue('')
    props.toggleVisibility()
  }

  return (
    <Dialog open={props.visibility} onClose={handleClose}>
      <DialogTitle>Edit Task</DialogTitle>
      <DialogContent>
        <TextField
          autoFocus
          id="name"
          label={'New value'}
          fullWidth
          value={inputValue}
          onChange={handleInputValueChange}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={handleSubmit} color="primary">
          Edit
        </Button>
        <Button onClick={handleClose} color="primary">
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  )
}

const mapStateToProps = (state) => ({
  tasks: state.tasks,
  visibility: state.dialogs.editDialogVisibility,
  editingTask: state.dialogs.currentTaskEditingId
})

const mapDispatchToProps = (dispatch) => {
  return {
    toggleVisibility: (payload) => dispatch(toggleEditDialogVisibility(payload)),
    editTask: (payload) => dispatch(editTask(payload))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(EditTask)
