import { Box } from "@material-ui/core"
import { connect } from "react-redux"
import Tasks from "./Tasks"

const TabPanel = (props) => {
  const selectedTab = props.tabs.find(tab => {
    return props.tabs.indexOf(tab) === props.selected
  })
  const filteredByTabTasks = props.tasks.filter(task => {
    return task.tabId == selectedTab.id
  })

  return (
    <Box>
      <Tasks tasks={filteredByTabTasks} />
    </Box>
  )
}

const mapStateToProps = (state) => ({
  selected: state.tabs.selected,
  tabs: state.tabs.list,
  tasks: state.tasks
})

const mapDispatchToProps = {
  
}


export default connect(mapStateToProps, mapDispatchToProps)(TabPanel)
