import { Checkbox, Divider, IconButton, ListItem, ListItemIcon, ListItemSecondaryAction, ListItemText, withStyles} from "@material-ui/core"
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { connect } from "react-redux";
import pt from 'prop-types'
import { deleteTask, toggleCompleted } from "store/tasksSlice";
import { setCurrentTaskEditingId, toggleEditDialogVisibility } from "store/dialogsSlice";
import { useRef } from "react";

const Task = (props) => {
  const handleDelete = () => {
    props.deleteTask({ id: props.id })
  }

  const handleToggleCompleted = () => {
    props.toggleCompleted({ id: props.id })
  }

  const handleEditTask = () => {
    props.toggleEditDialogVisibility()
    props.setCurrentTaskEditingId({ id: props.id })
  }

  return <>
    <ListItem button onClick={handleToggleCompleted}>

      <ListItemIcon>
        <StyledCheckbox
          checked={props.completed}
          name="checkedB"
          color="primary"
        />
      </ListItemIcon>

      <ListItemText
        primary={props.children}
        style={{ textDecoration: props.completed ? 'line-through' : 'none' }}
      />

      <ListItemSecondaryAction>
        <IconButton edge="end" onClick={handleEditTask}>
          <EditIcon />
        </IconButton>
        <IconButton edge="end" onClick={handleDelete}>
          <DeleteIcon color={'secondary'} />
        </IconButton>
      </ListItemSecondaryAction>

    </ListItem>
    <Divider />
  </>
}

const StyledCheckbox = withStyles({
  root: {
    color: '#66bb6a',
    '&$checked': {
      color: '#66bb6a',
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);

Task.propTypes = {
  children: pt.string.isRequired,
  id: pt.string.isRequired,
  completed: pt.bool.isRequired
}

const mapStateToProps = (state) => ({
  
})

const mapDispatchToProps =  {
  deleteTask,
  toggleCompleted,
  toggleEditDialogVisibility,
  setCurrentTaskEditingId
}


export default connect(mapStateToProps, mapDispatchToProps)(Task)
