import { createSlice } from "@reduxjs/toolkit"
import { v4 } from "uuid"


const tabsSlice = createSlice({
  name: 'tabs',
  initialState: {
    selected: 0,
    list: [
      {
        id: 'abc',
        name: 'Main',
      }
    ]  
  },
  reducers: {

    addTab: (state, action) => {
      const { name } = action.payload
      state.list.push({ id: v4(), name })
    },

    setTab: (state, action) => {
      const { to } = action.payload
      state.selected = to
    },

    deleteTab: (state, action) => {
      if (state.list.length > 1) {
        state.selected = 0
        state.list.splice(state.selected, 1) 
      }
    }

  }
})

export const { addTab, setTab, deleteTab } = tabsSlice.actions

export default tabsSlice.reducer
