import { Box, Button, Snackbar, TextField, withStyles } from "@material-ui/core"
import { useState } from "react"
import { connect } from "react-redux"
import { addTask } from "store/tasksSlice"

const AddTask = (props) => {
  const [inputValue, setInputValue] = useState('')
  const [errorVisibility, setErrorVisibility] = useState(false)

  const handleAddTask = () => {
    if (!inputValue) return
    const body = inputValue
    const tabId = props.tabs[props.selected].id
    props.addTask({ tabId, body })
    setInputValue('')
  }

  return <>
    <Box
      display={'flex'}
      paddingTop={2}
    >
      <Box
        flexGrow={'1'}
      >
        <StyledTextField
          id="outlined-basic"
          size={'small'}
          label="Task"
          fullWidth
          variant="outlined"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
        />
      </Box>
      <StyledButton
        variant={'contained'}
        color="secondary"
        onClick={handleAddTask}
      >
        Add task
      </StyledButton>
    </Box>
    {/* <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
      <Alert onClose={handleClose} severity="success">
        This is a success message!
      </Alert>
    </Snackbar> */}
  </>
}

const StyledTextField = withStyles({
  root: {
    '& > *': {
      borderRadius: '0'
    }
  }
})(TextField)

const StyledButton = withStyles({
  root: {
    borderRadius: '0',
    boxShadow: 'none'
  }
})(Button)

const mapStateToProps = (state) => ({
  tabs: state.tabs.list,
  selected: state.tabs.selected
})

const mapDispatchToProps = {
  addTask
}


export default connect(mapStateToProps, mapDispatchToProps)(AddTask)
