import { connect } from "react-redux"
import { AppBar, Box, Button, Paper, Tabs as MTabs } from '@material-ui/core'
import { Tab as MTab } from '@material-ui/core'
import pt from 'prop-types'
import { v4 } from "uuid"
import { useState } from "react"
import { setTab } from "store/tabsSlice"

const Tabs = (props) => {
  const names = props.tabs.map(tab => tab.name)

  const handleChange = (e, v) => {
    props.setTab(v)
  }

  return <>
    <Box paddingTop={1}>
      {/* <Paper square> */}
        <MTabs
          value={props.selected}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="auto"
          indicatorColor="primary"
          textColor="primary"
        >
          {names.map(name => (
            <MTab
              key={v4()}
              label={name}
            />
          ))}
        </MTabs>         
      {/* </Paper> */}
    </Box>   
  </>
}

Tabs.propTypes = {
  
}

const mapStateToProps = (state) => ({
  tabs: state.tabs.list,
  selected: state.tabs.selected
})

const mapDispatchToProps = (dispatch) => {
  return {
    setTab: (to) => dispatch(setTab({ to }))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(Tabs)